import React from 'react';
import AboutTab from './tabs/AboutTab';
import ContactTab from './tabs/ContactTab';
import ProjectsTab from './tabs/ProjectsTab';

const TabList = props => {
    return (
        <div>
            <div className="ui horizontal segment">
                <AboutTab />
            </div>
            <div className="ui horizontal segment">
                <ProjectsTab />
            </div>
            <div className="ui horizontal segment">
                <ContactTab />
            </div>
        </div>
    );
};

export default TabList;